}
/* 按钮 100% 宽度 */
.mdui-btn-block {
  display: block;
  width: 100%;
}
/* 密集型按钮 */
.mdui-btn-dense {
  height: 32px;
  line-height: 32px;
  font-size: 13px;
}
.mdui-btn-dense.mdui-btn-icon {
