 * ************   Fab 浮动操作按钮   ************
 * =============================================================================
 */
/* 浮动操作按钮 */
.mdui-fab {
  border-radius: 50%;
  font-size: 24px;
  margin: auto;
  padding: 0 !important;
  overflow: hidden;
  line-height: normal !important;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
  height: 56px;
  width: 56px;
  min-width: 56px;
}
.mdui-fab:hover {
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
.mdui-fab:active {
  box-shadow: 0 7px 8px -4px rgba(0, 0, 0, 0.2), 0 12px 17px 2px rgba(0, 0, 0, 0.14), 0 5px 22px 4px rgba(0, 0, 0, 0.12);
}
.mdui-fab .mdui-icon {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 24px;
  width: 24px;
  margin-left: 16px;
  margin-top: 16px;
}
/* mini 型浮动操作按钮 */
.mdui-fab-mini {
  height: 40px;
  width: 40px;
  min-width: 40px;
}
.mdui-fab-mini .mdui-icon {
  margin-left: 8px;
  margin-top: 8px;
}
/* 固定到右下角的 FAB 按钮 */
.mdui-fab-fixed,
.mdui-fab-wrapper {
  position: fixed !important;
  /* 手机平板上距离屏幕右下角 16px */
  right: 16px;
  bottom: 16px;
}
@media (min-width: 1024px) {
  .mdui-fab-fixed,
  .mdui-fab-wrapper {
    /* 电脑上距离屏幕右下角 24px */
    right: 24px;
    bottom: 24px;
  }
}
/* 含菜单的浮动操作按钮 */
.mdui-fab-wrapper {
  position: relative;
  width: 56px;
  height: 56px;
  padding-top: 8px;
  z-index: 4000;
  text-align: center;
}
.mdui-fab-wrapper > .mdui-fab .mdui-icon:not(.mdui-fab-opened) {
  will-change: opacity, transform;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 1;
}
.mdui-fab-wrapper > .mdui-fab .mdui-icon.mdui-fab-opened {
  will-change: opacity, transform;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  transform: rotate(225deg);
}
.mdui-fab-wrapper > .mdui-fab.mdui-fab-opened .mdui-icon:not(.mdui-fab-opened) {
  opacity: 0;
  transform: rotate(225deg);
}
.mdui-fab-wrapper > .mdui-fab.mdui-fab-opened .mdui-icon.mdui-fab-opened {
  opacity: 1;
  transform: rotate(360deg);
}
.mdui-fab-wrapper > .mdui-fab-mini {
  margin-top: 8px;
}
.mdui-fab-wrapper .mdui-fab-dial {
  visibility: visible;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 64px;
  text-align: center;
  height: 0;
}
.mdui-fab-wrapper .mdui-fab-dial .mdui-fab {
  margin: 8px 0;
  transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), color 0.2s cubic-bezier(0.4, 0, 0.2, 1), transform 150ms cubic-bezier(0.4, 0, 0.2, 1), opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  transform: scale(0);
  opacity: 0;
}
.mdui-fab-wrapper .mdui-fab-dial.mdui-fab-dial-show .mdui-fab {
  transform: scale(1);
  opacity: 1;
}
/* 添加类 .mdui-fab-hide 以动画的形式隐藏按钮 */
.mdui-fab,
.mdui-fab-mini,
.mdui-fab-wrapper {
  will-change: transform;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), transform 0.2s;
  transform: scale(1) translateZ(0);
}
.mdui-fab.mdui-fab-hide,
.mdui-fab-mini.mdui-fab-hide,
.mdui-fab-wrapper.mdui-fab-hide {
  transform: scale(0) translateZ(0);
}
