/**
 * =============================================================================
 * ************   Card 卡片   ************
 * =============================================================================
 */
/* 卡片 */
.mdui-card {
  box-sizing: border-box;
  position: relative;
  border-radius: 2px;
  overflow: hidden;
  color: #000;
  background-color: #fff;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
/**
 * ===================== 头部，包含头像、标题、副标题
 */
.mdui-card-header {
  box-sizing: border-box;
  padding: 16px;
  position: relative;
  height: 72px;
}
/* 卡片头部头像 */
.mdui-card-header-avatar {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  float: left;
}
/* 卡片头部标题 */
.mdui-card-header-title {
  display: block;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  margin-left: 52px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  opacity: 0.87;
}
/* 卡片头部副标题 */
.mdui-card-header-subtitle {
  display: block;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  margin-left: 52px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  opacity: 0.54;
}
/**
 * =========================== 主标题区域
 */
.mdui-card-primary {
  position: relative;
  padding: 24px 16px 16px 16px;
}
/* 主标题区域标题 */
.mdui-card-primary-title {
  display: block;
  font-size: 24px;
  line-height: 36px;
  opacity: 0.87;
}
/* 主标题区域副标题 */
.mdui-card-primary-subtitle {
  display: block;
  font-size: 14px;
  line-height: 24px;
  opacity: 0.54;
}
/**
 * ============================ 内容区域
 */
.mdui-card-content {
  position: relative;
  padding: 16px;
  line-height: 24px;
  font-size: 14px;
}
/**
 * ============================ 卡片菜单
 */
.mdui-card-menu {
  position: absolute;
  top: 16px;
  right: 16px;
  z-index: 1;
}
.mdui-card-menu .mdui-btn {
  margin-left: 8px;
}
/**
 * =========================== 按钮区域
 */
.mdui-card-actions {
  position: relative;
  box-sizing: border-box;
  padding: 8px;
}
.mdui-card-actions:before,
.mdui-card-actions:after {
  content: " ";
  display: table;
}
.mdui-card-actions:after {
  clear: both;
}
.mdui-card-actions:before,
.mdui-card-actions:after {
  content: " ";
  display: table;
}
.mdui-card-actions:after {
  clear: both;
}
.mdui-card-actions .mdui-btn {
  margin: 0 8px 0 0;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mdui-card-actions .mdui-btn-icon {
  width: 36px;
  height: 36px;
  margin: 0 8px;
}
/* 使按钮竖向排列 */
.mdui-card-actions-stacked .mdui-btn {
  margin: 0 0 4px 0;
  display: block;
}
.mdui-card-actions-stacked .mdui-btn:last-child {
  margin: 0;
}
/**
 * ============================= 媒体元素区域
 */
.mdui-card-media {
  position: relative;
}
.mdui-card-media img,
.mdui-card-media video {
  display: block;
  width: 100%;
}
/* 覆盖在媒体元素上的内容 */
.mdui-card-media-covered {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  /* 覆盖在媒体元素上的标题 */
}
.mdui-card-media-covered .mdui-card-primary-title {
  opacity: 1;
}
.mdui-card-media-covered .mdui-card-primary-subtitle {
  opacity: 0.7;
}
/* 覆盖在媒体元素顶部 */
.mdui-card-media-covered-top {
  bottom: auto;
  top: 0;
}
/* 覆盖层透明 */
.mdui-card-media-covered-transparent {
  background: transparent;
}
/* 覆盖层渐变 */
.mdui-card-media-covered-gradient {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.26), rgba(0, 0, 0, 0));
}
.mdui-card-media-covered-gradient.mdui-card-media-covered-top {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.26), rgba(0, 0, 0, 0));
}
/**
 * =============================================================================
 * ************   Card dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-card {
  background-color: #424242;
  color: #fff;
}
/**
 * =============================================================================
 * ************   Subheader 副标题   ************
 * =============================================================================
 *
 * 用于 List 和 Grid list 组件
 */
.mdui-subheader,
.mdui-subheader-inset {
  position: relative;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.54);
  height: 48px;
  line-height: 48px;
  cursor: default;
  padding-left: 16px;
  padding-right: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mdui-subheader-inset {
  padding-left: 72px;
}
/**
 * =============================================================================
 * ************   Subheader dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-subheader,
.mdui-theme-layout-dark .mdui-subheader-inset {
  color: rgba(255, 255, 255, 0.7);
}
/**
 * =============================================================================
 * ************   List 列表   ************
 * =============================================================================
 */
/* 列表 */
.mdui-list {
  margin: 0;
  padding: 8px 0;
  background-color: transparent;
  list-style: none;
}
.mdui-list .mdui-list {
  padding: 0;
}
.mdui-list > .mdui-divider,
.mdui-list > .mdui-divider-light,
.mdui-list > .mdui-divider-dark,
.mdui-list > .mdui-divider-inset,
.mdui-list > .mdui-divider-inset-light,
.mdui-list > .mdui-divider-inset-dark {
  margin-top: 8px;
  margin-bottom: 8px;
}
.mdui-list a {
  text-decoration: none;
  color: inherit;
}
.mdui-list .mdui-subheader,
.mdui-list .mdui-subheader-inset {
  margin-top: 8px;
}
.mdui-list .mdui-subheader:before,
.mdui-list .mdui-subheader-inset:before {
  content: ' ';
  background-color: rgba(0, 0, 0, 0.12);
  height: 1px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
}
.mdui-list .mdui-subheader:first-child,
.mdui-list .mdui-subheader-inset:first-child {
  margin-top: -8px;
}
.mdui-list .mdui-subheader:first-child:before,
.mdui-list .mdui-subheader-inset:first-child:before {
  background-color: transparent;
}
.mdui-list .mdui-subheader-inset:before {
  left: 72px;
}
/* 列表项 */
.mdui-list-item {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 16px;
  min-height: 48px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.mdui-list-item:hover {
  background-color: rgba(0, 0, 0, 0.08);
}
.mdui-list-item:after {
  content: ' ';
  height: 48px;
  visibility: hidden;
}
/* 列表项图标 */
.mdui-list-item-icon {
  min-width: 24px;
  width: 24px;
  height: 24px;
  color: rgba(0, 0, 0, 0.54);
}
/* 列表项头像 */
.mdui-list-item-avatar {
  min-width: 40px;
  max-width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 50%;
  background-color: #BDBDBD;
  color: #FFFFFF;
  margin-top: 8px;
  margin-bottom: 8px;
}
.mdui-list-item-avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
/* 列表项内容 */
.mdui-list-item-content {
  flex-grow: 1;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
}
/* 列表项内容的副内容 */
.mdui-list-item-text {
  opacity: 0.54;
  font-size: 14px;
}
.mdui-list-item-title ~ .mdui-list-item-text {
  margin-top: 4px;
}
/* 激活状态的列表项 */
.mdui-list-item-active {
  background-color: rgba(0, 0, 0, 0.08);
  font-weight: 700;
}
.mdui-list-item-active .mdui-list-item-content {
  font-weight: 700;
}
.mdui-list-item-active .mdui-list-item-text {
  font-weight: 400;
}
/* 限制文本高度 */
.mdui-list-item-one-line,
.mdui-list-item-two-line,
.mdui-list-item-three-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.mdui-list-item-one-line {
  -webkit-line-clamp: 1;
  height: 20px;
}
.mdui-list-item-two-line {
  -webkit-line-clamp: 2;
  height: 40px;
}
.mdui-list-item-three-line {
  -webkit-line-clamp: 3;
  height: 60px;
}
/* 列表项内的元素间添加间距 */
.mdui-list-item-icon ~ .mdui-list-item-content {
  margin-left: 32px;
}
.mdui-checkbox ~ .mdui-list-item-content,
.mdui-radio ~ .mdui-list-item-content,
.mdui-switch ~ .mdui-list-item-content {
  margin-left: 20px;
}
.mdui-list-item-avatar ~ .mdui-list-item-content {
  margin-left: 16px;
}
.mdui-list-item-content ~ .mdui-list-item-icon,
.mdui-list-item-content ~ .mdui-list-item-avatar,
.mdui-list-item-content ~ .mdui-checkbox,
.mdui-list-item-content ~ .mdui-radio,
.mdui-list-item-content ~ .mdui-switch {
  margin-left: 16px;
}
.mdui-list-item-content ~ .mdui-checkbox,
.mdui-list-item-content ~ .mdui-radio {
  padding-left: 24px;
}
/* 密集型列表 */
.mdui-list-dense {
  padding: 4px 0;
  font-size: 13px;
}
.mdui-list-dense > .mdui-divider,
.mdui-list-dense > .mdui-divider-light,
.mdui-list-dense > .mdui-divider-dark,
.mdui-list-dense > .mdui-divider-inset,
.mdui-list-dense > .mdui-divider-inset-light,
.mdui-list-dense > .mdui-divider-inset-dark {
  margin-top: 4px;
  margin-bottom: 4px;
}
.mdui-list-dense .mdui-subheader,
.mdui-list-dense .mdui-subheader-inset {
  margin-top: 4px;
  height: 40px;
  line-height: 40px;
  font-size: 12px;
}
.mdui-list-dense .mdui-subheader:first-child,
.mdui-list-dense .mdui-subheader-inset:first-child {
  margin-top: -4px;
}
.mdui-list-dense .mdui-list-item {
  min-height: 40px;
}
.mdui-list-dense .mdui-list-item:after {
  height: 40px;
}
.mdui-list-dense .mdui-list-item-icon {
  width: 20px;
  height: 20px;
  font-size: 20px;
}
.mdui-list-dense .mdui-list-item-avatar {
  min-width: 36px;
  min-height: 36px;
  height: 36px;
  width: 36px;
}
.mdui-list-dense .mdui-list-item-content {
  font-size: 13px;
  line-height: 18px;
  padding-top: 11px;
  padding-bottom: 11px;
}
.mdui-list-dense .mdui-list-item-text {
  font-size: 13px;
}
.mdui-list-dense .mdui-list-item-title ~ .mdui-list-item-text {
  margin-top: 2px;
}
.mdui-list-dense .mdui-list-item-one-line {
  height: 18px;
}
.mdui-list-dense .mdui-list-item-two-line {
  height: 36px;
}
.mdui-list-dense .mdui-list-item-three-line {
  height: 54px;
}
/**
 * =============================================================================
 * ************   List dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-list .mdui-subheader:before,
.mdui-theme-layout-dark .mdui-list .mdui-subheader-inset:before {
