}
[class*="mdui-color-"] .mdui-list-item-icon {
  color: inherit;
}
/**
 * =============================================================================
 * ************   Drawer 抽屉栏导航   ************
 * =============================================================================
 */
/* DOM 加载完后再添加 transition */
body.mdui-loaded {
  transition: padding 0.3s cubic-bezier(0, 0, 0.2, 1);
}
body.mdui-loaded .mdui-drawer {
  transition: all 0.3s cubic-bezier(0, 0, 0.2, 1);
}
/* 抽屉栏外层，默认出现在左侧且隐藏 */
.mdui-drawer {
  box-sizing: border-box;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 5000;
  will-change: transform;
  margin: 0;
  white-space: nowrap;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  top: 0;
  width: calc(100% - 56px);
  max-width: 280px;
}
@media (max-width: 1023px) {
  .mdui-drawer:not(.mdui-drawer-open) {
    box-shadow: none !important;
  }
}
@media (min-width: 600px) {
  .mdui-drawer {
    width: calc(100% - 64px);
    max-width: 320px;
  }
}
@media (min-width: 1024px) {
  .mdui-drawer {
    width: 240px;
    max-width: none;
  }
  .mdui-drawer.mdui-drawer-close {
    box-shadow: none !important;
  }
}
/* 出现在右侧的抽屉栏 */
.mdui-drawer-right {
  left: auto;
  right: 0;
}
/* 平板上的样式 */
@media (max-width: 1023px) {
  .mdui-drawer {
    /* 默认隐藏 */
    transform: translateX(-330px);
    /* 始终有背景和阴影 */
    background-color: #fff;
    box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2), 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);
  }
  .mdui-drawer-right {
    /* 右侧 drawer 也默认隐藏 */
    transform: translateX(330px);
  }
}
/* 手机上的样式 */
@media (max-width: 599px) {
  .mdui-drawer {
    transform: translateX(-290px);
  }
  .mdui-drawer-right {
    transform: translateX(290px);
  }
}
/* 强制隐藏抽屉栏 */
.mdui-drawer-close {
  transform: translateX(-290px);
}
.mdui-drawer-close.mdui-drawer-right {
  transform: translateX(290px);
}
@media (min-width: 600px) {
  .mdui-drawer-close {
    transform: translateX(-330px);
  }
  .mdui-drawer-close.mdui-drawer-right {
    transform: translateX(330px);
  }
}
@media (min-width: 1024px) {
  .mdui-drawer-close {
    transform: translateX(-250px);
  }
  .mdui-drawer-close.mdui-drawer-right {
    transform: translateX(250px);
  }
}
/* 强制显示抽屉栏 */
.mdui-drawer-open {
  transform: translateX(0) !important;
}
/* PC 上的样式 */
@media (min-width: 1024px) {
  /* 使该类所在元素获得 padding-left 或 padding-right，避免被抽屉栏覆盖住页面 */
  .mdui-drawer-body-left {
    padding-left: 240px;
  }
  .mdui-drawer-body-right {
    padding-right: 240px;
  }
  /* PC 上默认有上边距 */
  .mdui-appbar-with-toolbar .mdui-drawer {
    top: 64px;
  }
  .mdui-appbar-with-tab .mdui-drawer {
    top: 48px;
  }
  .mdui-appbar-with-tab-larger .mdui-drawer {
    top: 72px;
  }
  .mdui-appbar-with-toolbar.mdui-appbar-with-tab .mdui-drawer {
    top: 112px;
  }
  .mdui-appbar-with-toolbar.mdui-appbar-with-tab-larger .mdui-drawer {
    top: 136px;
  }
}
/* 抽屉栏不是透明时添加阴影 */
.mdui-drawer[class*="mdui-color-"]:not(.mdui-color-transparent) {
  box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2), 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);
}
/* 抽屉栏 100% 高度，覆盖在导航栏上面 */
.mdui-drawer-full-height {
  top: 0 !important;
}
/**
