  background-color: #5d5d5d;
}
/**
 * =============================================================================
 * ************   Chip 纸片   ************
 * =============================================================================
 */
.mdui-chip {
  box-sizing: border-box;
  display: inline-block;
  height: 32px;
  border-radius: 16px;
  white-space: nowrap;
  background-color: #e0e0e0;
  cursor: pointer;
  user-select: none;
  margin: 2px 0;
  will-change: box-shadow;
  transition: box-shadow 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}
.mdui-chip:hover,
.mdui-chip:focus {
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.mdui-chip:active {
  background-color: #d6d6d6;
}
/* 左侧的图标 */
.mdui-chip-icon {
  position: relative;
  height: 32px;
  width: 32px;
  border-radius: 50%;
  margin-right: -4px;
  font-size: 18px;
  line-height: 32px;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  text-align: center;
  background-color: #989898;
  color: #ffffff;
}
.mdui-chip-icon .mdui-icon {
  position: absolute;
  left: 4px;
  top: 4px;
  color: #ffffff;
}
/* 文本 */
.mdui-chip-title {
  height: 32px;
  line-height: 32px;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 14px;
  vertical-align: middle;
  display: inline-block;
}
/* 删除按钮 */
.mdui-chip-delete {
  height: 24px;
  width: 24px;
  opacity: 0.54;
  cursor: pointer;
  margin-left: -8px;
  margin-right: 4px;
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  text-align: center;
  border-radius: 50%;
  will-change: opacity;
  transition: opacity 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}
.mdui-chip-delete:hover,
.mdui-chip-delete:focus {
  opacity: 0.87;
}
/**
 * =============================================================================
 * ************   Chip dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-chip {
  background-color: #484848;
}
.mdui-theme-layout-dark .mdui-chip:active {
  background-color: #5d5d5d;
}
/**
