  color: inherit;
  text-decoration: none;
  font-size: 12px;
  text-align: center;
  cursor: pointer;
  text-overflow: ellipsis;
  overflow: hidden;
  user-select: none;
  opacity: 0.7;
  min-width: 32px;
  max-width: none;
  padding: 8px 12px 10px 12px;
  flex: 1;
