 */
.mdui-theme-primary-blue .mdui-bottom-nav a.mdui-bottom-nav-active {
  color: #2196F3;
}
/* 带背景色的底部导航栏 */
.mdui-bottom-nav[class*="mdui-color-"] .mdui-bottom-nav-active {
  color: inherit !important;
}
/**
 * =============================================================================
 * ************   Menu 菜单   ************
 * =============================================================================
 */
/* 菜单 */
.mdui-menu {
  box-sizing: border-box;
  padding: 8px 0;
  margin: 0;
  font-size: 16px;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  width: 168px;
  background-color: #fff;
  color: rgba(0, 0, 0, 0.87);
  list-style: none;
  display: block;
  z-index: 99999;
  opacity: 0;
  will-change: transform, opacity, visibility;
  transition-property: transform, opacity, visibility;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
  visibility: hidden;
  transform: scale(0);
  position: fixed;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.mdui-menu .mdui-divider {
  margin-top: 8px;
  margin-bottom: 8px;
}
/* 打开状态的菜单 */
.mdui-menu-open {
  visibility: visible;
  transform: scale(1);
  opacity: 1;
}
/* 关闭中的菜单 */
.mdui-menu-closing {
  visibility: visible;
  transform: scale(1);
  opacity: 0;
}
/* 菜单项 */
.mdui-menu-item {
  position: relative;
}
.mdui-menu-item > a {
  position: relative;
  display: block;
  color: inherit;
  text-decoration: none;
  height: 48px;
  line-height: 48px;
  padding: 0 16px;
  user-select: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mdui-menu-item > a:hover {
  background-color: #EEEEEE;
}
.mdui-menu-item > .mdui-menu {
  position: absolute;
  box-shadow: 0 5px 6px -3px rgba(0, 0, 0, 0.2), 0 9px 12px 1px rgba(0, 0, 0, 0.14), 0 3px 16px 2px rgba(0, 0, 0, 0.12);
}
.mdui-menu-item[disabled] > a {
  cursor: default;
  color: rgba(0, 0, 0, 0.38) !important;
}
.mdui-menu-item[disabled] > a:hover {
  background-color: inherit !important;
}
.mdui-menu-item[disabled] > a .mdui-icon {
  color: rgba(0, 0, 0, 0.26);
}
/* 激活状态的菜单项 */
.mdui-menu-item-active {
  background-color: #EEEEEE;
}
/* 菜单中的图标 */
.mdui-menu-item-icon {
  color: rgba(0, 0, 0, 0.54);
  width: 40px;
  padding-right: 16px;
  box-sizing: border-box;
  display: inline-block;
}
/* 菜单中的辅助文本或图标 */
.mdui-menu-item-helper {
  float: right;
}
/* 有子菜单的条目的图标 */
.mdui-menu-item-more {
  float: right;
  width: 24px;
  height: 24px;
  margin: 4px 0;
  background-image: url('./menu/svg/arrow_right_dark.svg');
}
/* 级联菜单 */
.mdui-menu-cascade {
  padding: 16px 0;
  font-size: 15px;
  width: 320px;
  overflow-y: visible;
}
.mdui-menu-cascade > .mdui-menu-item > a {
  height: 32px;
  line-height: 32px;
  padding: 0 24px;
}
/**
 * =============================================================================
 * ************   Menu dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-menu {
  background-color: #424242;
  color: #fff;
}
.mdui-theme-layout-dark .mdui-menu-item > a:hover {
  background-color: #616161;
}
.mdui-theme-layout-dark .mdui-menu-item[disabled] > a {
  color: rgba(255, 255, 255, 0.5) !important;
}
.mdui-theme-layout-dark .mdui-menu-item[disabled] > a .mdui-icon {
  color: rgba(255, 255, 255, 0.3);
}
.mdui-theme-layout-dark .mdui-menu-item-active {
  background-color: #616161;
}
.mdui-theme-layout-dark .mdui-menu-item-icon {
  color: #ffffff;
}
.mdui-theme-layout-dark .mdui-menu-item-more {
  background-image: url('./menu/svg/arrow_right_light.svg');
}
